.Burger {
  width: 200px;
  height: 200px;
  margin: auto;
  overflow: scroll;
  text-align: center;
  font-weight: bold;
  font-size: 1.2rem;

  @media (min-width: 300px) and (min-height: 200px) {
    width: 250px;
    height: 200px;
  }

  @media (min-width: 500px) and (min-height: 400px) {
    width: 350px;
    height: 300px;
  }

  @media (min-width: 1000px) and (min-height: 700px) {
    width: 450px;
    height: 400px;
  }
}